import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  aboutMe = [`I am self-taught developer, who is always trying to learn more in order 
            write cleaner and more efficient code. I place high emphasis on knowing about the
            why behind the how.`,
            `I started learning how to program games at first, but migrated over to web design.
             I find the UI / UX portion of web development to be the most challenging, but am working on improving my skills
             every day. I find most joy in the creation and programming of websites.`];
  constructor() { }

  ngOnInit() {
  }

}



