import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {
  websites: any[] = [
    {
      title: 'ngMessenger',
      target: "https://bit.ly/ngmessenger",
      image: "/assets/screencaps/ngmessenger.png",
      skills: "M.E.A.N (Mongoose,Express,Angular,NodeJs) Stack SPA ",
      description: `Full-stack project created using the MEAN stack. Custom backend with route protection and custom error handling.
                    Uses custom MongoDB to store users and messages, and their relations. Uses lazy loading for registration and AOT compiling.
                    Authorization for each message tied to user authentication.`
    }, {
      title: 'ngProject',
      target: "https://mszauer.github.io/ngProject/",
      image: "/assets/screencaps/ngproject.png",
      skills: "Angular (Angular2) Front-End, ES6",
      description: `SPA Built using the latest Angular(2) build and features. Mimicks backend server calls with API calls. 
                    Uses RESTful routing and is similar to blog website. Built using re-usable components and uses Observables to handle asynchrony.`
    },
    {
      title: 'YelpCamp',
      target: "https://www.bit.ly/yelpcamp",
      image: "/assets/screencaps/yelpcamp.png",
      skills: "MongooseDB, Express, EJS, NodeJS, Passport, ES6",
      description: `Full-stack project that implements RESTful routing and is like Yelp but for campsites. Registered users can
                    add, rate, and review campsites. `
    }];
  constructor() { }

  ngOnInit() {
  }

}



